  border-radius: 12px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  overflow: hidden;
  margin-top: 20px;
}

.table-responsive {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.attendees-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;
}

.attendees-table thead {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
}

.attendees-table th {
  padding: 16px 12px;
  text-align: right;
  font-weight: 600;
  color: #374151;
  border-bottom: 2px solid #e5e7eb;
  font-size: 0.875rem;
  white-space: nowrap;
}

.attendees-table td {
  padding: 16px 12px;
  border-bottom: 1px solid #f3f4f6;
  vertical-align: top;
}

.attendee-row:hover {
  background-color: #f9fafb;
}

.attendee-row:last-child td {
  border-bottom: none;
}

/* Table Cell Styles */
.attendee-name-cell {
  min-width: 180px;
}

.attendee-name {
  font-weight: 600;
  color: #1f2937;
  margin-bottom: 4px;
}

.attendee-org {
  font-size: 0.75rem;
  color: #6b7280;
  margin-bottom: 2px;
}

.attendee-job {
  font-size: 0.75rem;
  color: #9ca3af;
}

.status-cell {
  text-align: center;
  min-width: 100px;
}

.kind-cell {
  text-align: center;
  min-width: 80px;
}

.role-cell {
  text-align: center;
  min-width: 80px;
}

.email-cell {
  min-width: 150px;
}

.email-text {
  font-size: 0.8rem;
  color: #374151;
  word-break: break-all;
}

.department-cell {
  min-width: 120px;
}

.department-text {
  font-size: 0.8rem;
  color: #374151;
}

.no-data {
  color: #9ca3af;
  font-style: italic;
}

.actions-cell {
  text-align: center;
  min-width: 120px;
}

.action-buttons {
  display: flex;
  gap: 4px;
  justify-content: center;
  align-items: center;
}

.action-buttons .btn-sm {
  padding: 6px;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.action-buttons .btn-sm:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}

.action-buttons .btn-primary {
  background-color: #B68A35;
  color: #FFFFFF;
}

.action-buttons .btn-primary:hover {
  background-color: #B68A35;
}

.action-buttons .btn-secondary {
  background-color: #B68A35;
  color: #FFFFFF;
}

.action-buttons .btn-secondary:hover {
  background-color: #B68A35;
}

.action-buttons .btn-danger {
  background-color: #ef4444;
  color: white;
}

.action-buttons .btn-danger:hover {
  background-color: #dc2626;
}

/* Status Badges */
.status-badge {
  display: inline-block;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 500;
  text-align: center;
  min-width: 60px;
}

.status-0 {
  background-color: #dbeafe;
  color: #1e40af;
}

.status-1 {
  background-color: #dcfce7;
  color: #166534;
}

.status-2 {
  background-color: #fef2f2;
  color: #dc2626;
}

.status-3 {
  background-color: #f0fdf4;
  color: #15803d;
}

.status-4 {
  background-color: #fef3c7;
  color: #d97706;
}

/* Kind and Role Badges */
.kind-badge,
.role-badge {
  display: inline-block;
  padding: 3px 8px;
  border-radius: 8px;
  font-size: 0.7rem;
  font-weight: 500;
}

.kind-internal {
  background-color: #e0e7ff;
  color: #3730a3;
}

.kind-external {
  background-color: #fef3c7;
  color: #92400e;
}

.role-speaker {
  background-color: #fce7f3;
  color: #be185d;
}

.role-attendee {
  background-color: #e0f2fe;
  color: #0369a1;
}

/* Responsive Table */
@media (max-width: 1024px) {
  .attendees-table th,
  .attendees-table td {
    padding: 12px 8px;
    font-size: 0.8rem;
  }

  .attendee-name-cell {
    min-width: 150px;
  }

  .email-cell {
    min-width: 120px;
  }
}

@media (max-width: 768px) {
  .attendees-table-container {
    margin: 0 -16px;
    border-radius: 0;
  }

  .attendees-table th,
  .attendees-table td {
    padding: 10px 6px;
    font-size: 0.75rem;
  }

  .attendee-name-cell {
    min-width: 120px;
  }

  .status-cell,
  .kind-cell,
  .role-cell {
    min-width: 60px;
  }

  .email-cell,
  .department-cell {
    min-width: 100px;
  }

  .actions-cell {
    min-width: 80px;
  }

  .action-buttons {
    flex-direction: column;
    gap: 2px;
  }

  .action-buttons .btn-sm {
    padding: 4px;
  }
}

@media (max-width: 640px) {
  .attendees-table th:nth-child(5),
  .attendees-table td:nth-child(5),
  .attendees-table th:nth-child(6),
  .attendees-table td:nth-child(6) {
    display: none;
  }
}

/* Delete Attendee Modal Styles */
.attendee-preview {
  background-color: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 16px;
  margin-top: 16px;
}

.preview-label {
  font-weight: 600;
  color: #475569;
  margin-bottom: 8px;
  font-size: 0.875rem;
}

.preview-text {
  color: #1e293b;
  font-size: 1rem;
  font-weight: 500;
  margin-bottom: 8px;
}

.preview-details {
  margin-top: 8px;
}

.preview-detail {
  font-size: 0.8rem;
  color: #64748b;
  margin-bottom: 4px;
}
